<template>
    <el-dialog :visible.sync="dialogShow" append-to-body :before-close="closeDialog" width="720px" class="project-edit">
        <div slot="title">
            <div class="tit">新建项目</div>
        </div>
        <el-form ref="form" :model="project" :rules="rules" label-width="110px" class="form">
            <el-form-item label="项目名称：" prop="project_name">
                <el-input v-model="project.project_name" placeholder="请输入项目名称"></el-input>
            </el-form-item>
            <el-form-item label="客户名称：" prop="customer_name">
                <el-input v-model="project.customer_name" placeholder="请输入客户名称"></el-input>
            </el-form-item>
            <el-form-item label="资金来源：">
                <el-select v-model="project.capital_type" placeholder="请选择资金来源">
                    <el-option v-for="(item,i) in capitalTypes" :key="i" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="招标方式：">
                <el-select v-model="project.tender_type" placeholder="请选择招标方式">
                    <el-option v-for="(item,i) in tenderTypes" :key="i" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="项目进展：" prop="progress_id">
                <el-select v-model="project.progress_id" placeholder="请选择销售阶段">
                    <el-option v-for="(item,i) in projectProgressTypes" :key="i" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <!-- 采购意向 -->
            <div class="tit mb20">采购意向</div>
            <el-form-item label="需求概述：">
                <el-input v-model="project.demand_summary" placeholder="请输入需求概述"></el-input>
            </el-form-item>
            <el-form-item label="资金预算：">
                <el-input v-model="project.budget" placeholder="请输入资金预算"></el-input>
            </el-form-item>
            <el-form-item label="历史供应商：">
                <el-input v-model="project.history_supplier" placeholder="请输入历史供应商"></el-input>
            </el-form-item>
            <el-form-item label="竞争产品：">
                <el-input v-model="project.competitor" placeholder="请输入竞争产品"></el-input>
            </el-form-item>
            <!-- 项目干系人 -->
            <div class="tit mb20">项目干系人</div>
            <div class="el-form-item" v-for="(linkMan, i) in project.stakeholders" :key="i">
                <div class="el-form-item__label w110">干系人：</div>
                <div class="el-form-item__content ml110">
                    <el-row>
                        <el-col :span="12" class="pr10">
                            <el-form-item label-width="0">
                                <el-input v-model="linkMan.name" placeholder="请输入姓名"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="pr10">
                            <el-form-item label-width="0">
                                <el-input v-model="linkMan.contact" placeholder="联系方式"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="pr10">
                            <el-form-item label-width="0">
                                <el-select v-model="linkMan.role_type" class="wp100" placeholder="请选择角色">
                                    <el-option v-for="(item,i) in roleTypes" :key="i" :label="item.label" :value="item.value"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="pr10">
                            <el-form-item label-width="0">
                                <el-input v-model="linkMan.post" placeholder="请输入职务"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-button type="primary" plain @click="addContactMan" size="mini">添加干系人</el-button>
                </div>
            </div>
            <el-form-item label="备注：">
                <el-input type="textarea" v-model="project.name"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="tc">
            <el-button type="primary" @click="doSave">确 定</el-button>
            <el-button @click="doCancel">取 消</el-button>
        </div>
    </el-dialog>
</template>
<script>
import * as constants from './constants.js';
import {capitalTypes ,tenderTypes,roleTypes,projectProgressTypes} from '@/views/const.js';

export default {
    props: {
        value: {
            default: false
        },
        project: {
            default (){
                return constants.initProject()
            }
        }
    },
    data() {
        return {
            rules: constants.initRule(),
            capitalTypes, // 资金来源
            tenderTypes, // 招标方式
            roleTypes, // 干系人角色
            projectProgressTypes, // 进展
            dialogShow: this.value,
            refForm: null,
        };
    },
    watch: {
        value(n, o) {
            this.dialogShow = n
        },
        dialogShow(n, o) {
            this.$emit('input', n)
            this.$nextTick(()=>{
                if (!this.refForm) {
                    this.refForm = this.$refs.form
                }
                this.refForm.resetFields()
            })
        },
        projectId(n,o) {
            if (n) {
                this.loadProjectDetail()
            }

        }
    },
    mixins: [],

    methods: {
        // 编辑项目加载项目详情
        loadProjectDetail(){
            console.log('loadProjectDetail')
        },
        closeDialog() {
            this.dialogShow = false
        },
        doSave() {
            if (this.project.id) {
                // 编辑
                this.$api.project.updateProjetMan(this.project).then(data => {
                    this.$emit('on-save')
                })
            }else{
                // 新增
                this.$api.project.addProject(this.project).then(data => {
                    this.$emit('on-save')
                })
            }
            
        },
        doCancel() {
            console.log('doCancel')
            this.closeDialog()
        },
        addContactMan() {
            console.log('addContactMan')
        }
    },
    created() {},
    mounted() {

    },
};
</script>
<style lang="scss">
.project-edit {
    .tit {
        line-height: 2.5;
        border-bottom: 1px solid #ddd;
        font-size: 14px;
    }
    .el-form-item .el-form-item {
        margin-bottom: 22px;
    }
    .form {
        width: 540px;
        margin: 0 auto;
    }
    .ml110 {
        margin-left: 110px;
    }
}
</style>


