// 列表查询条件
export const projectListQuery = {
    project_name: '',
    customer_name: '',
    industry_type_id: '',
    progress: '',
    director_name: '',
    create_time: null,
};
export function initProjectListQuery() {
    return Object.assign({},projectListQuery)
}

// test data
export function getTestListData() {
    return [
        {
            id: 'id', //  String  项目Id    
            project_no: 'project_no', //  String  项目编号    
            project_name: 'project_name', //    String  项目名称    
            customer_id: 'customer_id', // String  客户Id    
            customer_name: 'customer_name', //   String  客户名称    
            stakeholder_name: 'stakeholder_name', //    String  联系人 
            stakeholder_contact: 'stakeholder_contact', // String  联系方式    
            progress_name: '1', //   String  项目进展    
            create_time: 'create_time', // Long    创建时间    
            customer_level: 1, //  Byte    客户级别    1：VIP 2：重要客户 3：普通客户
            director_name: 'director_name', //   String  负责人 
        }
    ]
};

// 初始化新增项目
export const stakeholders = {
    name: 'test_name',
    contact: 'test_contact',
    post: 'test_post',
    role_type: 'test_role_type',
};
export const project = {
    project_name: 'test_project_name',
    customer_id: '',
    customer_name: 'test_customer_name',
    capital_type: 1,
    tender_type: 1,
    progress_id: '1',
    demand_summary: 'test_demand_summary',
    budget: 20,
    history_supplier: 'test_history_supplier',
    competitor: 'test_competitor',
    stakeholders: [],
    project_remark: 'test_project_remark',
};

export function initProject() {
    let obj = {...project}
    console.log('project',project)
    obj.stakeholders = [{...stakeholders}]
    return obj
}

// 新增编辑 rule
export function initRule() {
    return {
        project_name: [
            {required: true, message: '必填字段'},
            { min: 2, max: 50, message: '长度在 2 到 50 个字符' }
        ],
        customer_name: [
            {required: true, message: '必填字段'},
            { min: 2, max: 50, message: '长度在 2 到 50 个字符' }
        ],
        progress_id: [
            {required: true, message: '必填字段'}
        ],
        stakeholders: []
    }
};






